<template>
  <div class="order-ad">
    <div class="title clearfix">
      {{ $t("navBread.myOrder") }}:
    </div>
    <el-form :inline="true"  class="order-search clearfix">
      <el-select v-model="selectCoinID" :placeholder="$t('myOrder.part2')" class="coin" @change="handleSelect">
      <el-option
        v-for="item in selectList"
        :key="item.currency_id"
        :label="item.currency_mark"
        :value="item.currency_id">
      </el-option>
    </el-select>
    <div class="pull-right">
      <el-form-item>
          <el-form-item>
            <el-date-picker type="date" :placeholder="$t('queryByTime.beginPlaceorder')" class="time-input" v-model="start_time"></el-date-picker>
          </el-form-item>{{ $t('queryByTime.to') }}&nbsp;
          <el-form-item >
            <el-date-picker type="date" :placeholder="$t('queryByTime.endPlaceorder')" class="time-input" v-model="end_time"></el-date-picker>
          </el-form-item>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="getmyAdList">{{ $t('queryByTime.query') }}</el-button>
      </el-form-item>
    </div>
    </el-form>
    <el-table
    :data="tableData" class="data-table">
      <el-table-column :label="$t('myOrder.part3')" width="100">
        <template slot-scope="scope">
          <div class="time" style="color: #a9afbb">{{ scope.row.orders_time }}</div>
        </template>
      </el-table-column>
      <el-table-column
        prop="trade_type"
        :label="$t('myOrder.part4')"
        :filters="[{ text: $t('myOrder.part5'), value: 'buy' }, { text: $t('myOrder.part6'), value: 'sell' }]"
        :filter-method="filterTag" filter-placement="bottom-end">
        <template slot-scope="scope">
            <span v-if="scope.row.trade_type == 'buy'" class="c-green">{{ $t('myOrder.part5') }}</span>
            <span v-if="scope.row.trade_type == 'sell'" class="c-red">{{ $t('myOrder.part6') }}</span>
        </template>
      </el-table-column>
      <el-table-column :label="$t('myTrade.part1')">
        <template slot-scope="scope">
          {{ scope.row.trade_price }}
        </template>
      </el-table-column>
      <el-table-column :label="$t('myTrade.part2')">
        <template slot-scope="scope">
          {{ scope.row.trade_total_num }}
        </template>
      </el-table-column>
      <el-table-column :label="$t('myTrade.part3')">
        <template slot-scope="scope">
          {{ scope.row.trade_success_num }}
        </template>
      </el-table-column>
      <el-table-column :label="$t('myTrade.part4')">
        <template slot-scope="scope">
          {{ scope.row.trade_faild_num }}
        </template>
      </el-table-column>
      <el-table-column prop="status" :label="$t('myTrade.part5')"
      :filters="[{ text: $t('myTrade.part6'), value: 1 },{ text: $t('myTrade.part7'), value: 2 },{ text: $t('myTrade.part8'), value: -1 }, { text: $t('myTrade.part9'), value: 0 }]" :filter-method="filterTag1" filter-placement="bottom-end">
      <template slot-scope="scope">
        <div v-if="scope.row.status == '1'" >{{ $t('myTrade.part6') }}</div>
        <div v-if="scope.row.status == '2'" >{{ $t('myTrade.part7') }}</div>
        <div v-if="scope.row.status == '-1'" >{{ $t('myTrade.part8') }}</div>
        <div v-if="scope.row.status == '0'" >{{ $t('myTrade.part9') }}</div>
        </template>
      </el-table-column>
      <el-table-column
        :label="$t('myTrade.part11')">
        <template slot-scope="scope" >
          <div class="swicth-btn">
            <a href="javascript:void(0)" v-if="scope.row.status == 0 || scope.row.status == 1" class="cancel"  @click="handleRevoke(scope.row.orders_id)">{{ $t('myTrade.part10') }}</a>
          </div>
          
        </template>
      </el-table-column>
  </el-table>
  <el-pagination
    background
    layout="total, prev, pager, next, jumper"
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="pageOptions.page"
    :page-sizes="[10, 20, 30, 40]"
    :page-size="pageOptions.per_page_num"
    :total="totalCount" class="page">
  </el-pagination>
  </div>
</template>
<script>
  import { entrustCancelOption } from '@/api/trade'
  import { getEntrustTradeList, financeCenter } from '@/api/orderManage'
  export default {
    data() {
      return {
        selectCoinID: null,
        selectList: [],
        options: [],
        start_time: null,
        end_time: null,
        // 分页选项
        pageOptions: {
          per_page_num: 10,
          page: 1
        },
        totalCount: 0,
        tableData: null
      }
    },
    created() {
      this.getSelectList()
    },
    methods: {
      handleSelect() {
        this.getmyAdList()
        window.location.href = window.location.href.split('?')[0] + '?currency_id=' + this.selectCoinID
      },
      handleRevoke(orders_id) {
        this.$confirm(this.$t('add.cancleOrder'), this.$t('dialog.title'), {
          confirmButtonText: this.$t('dialog.sure'),
          cancelButtonText: this.$t('dialog.cancle'),
          type: 'warning'
        }).then(() => {
          const data = {
            'currency_trade_id': this.$store.state.tradeDetail.trade_area_id,
            'currency_id': this.selectCoinID,
            'orders_id': orders_id
          }
          entrustCancelOption(data).then(res => {
            if (res.status !== 1000) {
              this.$message.error(this.$t('myOrder.part12'))
              return false
            }
            this.$message.success(this.$t('myOrder.part13'))
            this.getmyAdList()
          }).catch(() => {
          })
        }).catch(() => {
        })
      },
      handSendAd() {
        this.$router.push({ name: 'release', query: {}})
      },
      handleSizeChange(val) {
        this.pageOptions.page = val
        this.getmyAdList()
      },
      handleCurrentChange(val) {
        this.pageOptions.page = val
        this.getmyAdList()
      },
      getSelectList() {
        financeCenter().then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }
          this.selectList = [...res.data.normal_currency]
          if (this.$route.query.currency_id) {
            this.selectCoinID = parseFloat(this.$route.query.currency_id)
          } else {
            this.selectCoinID = this.selectList[0].currency_id
          }
          this.getmyAdList()
        }).catch(() => {
        })
      },
      filterTag(value, row) {
        return row.trade_type === value
      },
      filterTag1(value, row) {
        return row.status === value
      },
      filterHandler(value, row, column) {
        const property = column['property']
        return row[property] === value
      },
      getmyAdList() {
        // let data = {...this.pageOptions}
        const data = {
          length: this.pageOptions.per_page_num,
          start: (this.pageOptions.page - 1) * 10
        }
        if (this.selectCoinID) {
          data.currency_id = this.selectCoinID
        }
        data.trade_status = -2

        if (this.start_time) {
          data.start_time = Date.parse(this.start_time) / 1000
        }
        if (this.end_time) {
          data.end_time = Date.parse(this.end_time) / 1000
        }
        getEntrustTradeList(data).then(res => {
          if (res.status !== 1000) {
            this.$message.error(res.msg)
          }

          this.tableData = [...res.data.order_list]
          this.totalCount = res.recordsTotal
        }).catch(() => {
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .order-ad{
    background: white;
    padding: 20px;
    min-height: 770px;
    .swicth-btn a.normal {
        color: #7d58d1;
    }
    .swicth-btn a {
        margin-right: 0 !important;
    }

    .swicth-btn a {
      color: #7d58d1;
        display: inline-block;
        padding: 3px 12px;
        margin-right: 18px;
        border: 1px solid #7d58d1;
        border-radius: 5px;
        font-size: 12px;
    }
    .title{
      padding-bottom: 10px;
      font-size: 16px;
      line-height: 30px;
      border-bottom: 1px solid #ebecf1;
    }
    .order-search{
      position: relative;
      padding-top: 25px;
      .time-input{
        width: 140px;
      }
      .coin{
        width: 150px;
      }
      
    }
    .data-table{
      span{
        color: #8e6ed7;
        cursor: pointer;
        margin-left: 6px;
      }
      .time{
        color: #a9afbb;
      }
    }
  }
</style>


